import React, { useState } from "react";
import "./LootDrop.scss";
import { config } from "../../config/env";

export default function LootDrop() {
  const [locker, SetLocker] = useState("");
  const [token, SetToken] = useState("");
  const [downloadedText, SetDownloadedText] = useState("");
  const DownloadSupply = () => {
    console.log(token);
    console.log(locker);
    if (locker == "" || token == "") {
      if (locker == "") {
        alert("Please enter the Locker number");
      }
      if (token == "") {
        alert("Please enter the valid Token number");
      }
    } else {
      const myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");

      // const formData = new FormData();
      // formData.append('locker', 9999);
      // formData.append('token', '1');
      fetch(config.api + "supply/loot/text", {
        method: "POST",
        body: JSON.stringify({
          locker: locker,
          token: token,
        }), //formData,
        headers: myHeaders,
        // mode: 'no-cors'
      })
        .then((response) => {
          console.log(response);
          return response.text();
        })
        .then((res) => {
          SetDownloadedText(res);
          console.log(res);
        })
        .catch((e) => {
          console.log(e);
        });
    }
    // alert("You have sucessfully Looted the Drop");
  };
  return (
    <div className="container loot-drop">
      <form className="h-100">
        <div className={"row " + (downloadedText == "" ? "h-75" : "mt-5")}>
          <div
            className={
              "form-group locker col-12 col-lg-6 m-auto " +
              (downloadedText != "" ? "mb-4" : "")
            }
          >
            <label>Enter the Locker Number</label>
            <input
              type="text"
              className="form-control p-3"
              placeholder="Enter the locker number"
              required
              value={locker}
              onChange={(e) => SetLocker(e.target.value)}
            />
          </div>
          <div
            className={
              "form-group token  col-12 col-lg-6 m-auto " +
              (downloadedText != "" ? "mb-4" : "")
            }
          >
            <label>Enter the token number associated with this locker</label>
            <input
              type="text"
              className="form-control p-3"
              placeholder="Enter the Token number"
              required
              value={token}
              onChange={(e) => SetToken(e.target.value)}
            />
          </div>
        </div>
        <div className="row m-2">
          <button
            type="button"
            className="btn btn-success"
            onClick={DownloadSupply}
          >
            Loot Supply From Drop
          </button>
        </div>
        {downloadedText != "" ? (
          <div className="row m-2 text-white">
            <h6>
              Text from Locker: <b style={{ color: "#00ffc9" }}>{locker}</b>
            </h6>
            <h4 style={{ color: "#fffa00" }}>{downloadedText}</h4>
          </div>
        ) : (
          <></>
        )}
      </form>
    </div>
  );
}
